const formatCash = maxPrecision => n => {
  if (n < 1e6) return n.toLocaleString(undefined, { minimumFractionDigits: 0 });
  // if (n >= 1e3 && n < 1e6) return +(n / 1e3).toFixed(maxPrecision) + 'K';
  if (n >= 1e6 && n < 1e9) return +(n / 1e6).toFixed(maxPrecision) + 'M';
  if (n >= 1e9 && n < 1e12) return +(n / 1e9).toFixed(maxPrecision) + 'B';
  if (n >= 1e12) return +(n / 1e12).toFixed(maxPrecision) + 'T';
};

export default function switches($) {
  const switches = $('.js-switch');
  const cards = $('.c-pc');

  if (switches.length) {
    switches.each((_, el) => {
      $(el).on('click', function(e) {
        e.stopPropagation();

        $(this)
          .closest('.c-p-switch')
          .toggleClass('c-p-switch--checked');

        $(this)
          .closest('.o-container')
          .find('.js-price')
          .toggleClass('js-price--show');

        $(this)
          .closest('.o-container')
          .find('.c-pc')
          .find('.js-discount')
          .toggleClass('js-discount--hide');
      });
    });
  }

  if (cards.length) {
    cards.each((_, el) => {
      const price = parseInt($(el).data('price'));
      const discount = parseInt($(el).data('discount'));
      if (price === 0 || price === '0') return;
      const formatSmall = formatCash(0);
      const annualPrice = parseInt(price) * 12;
      const input = $(el).find('.js-input');
      const monthText = $(el).find('.js-price--month');
      const yearText = $(el).find('.js-price--year');
      // const discountText = $(el).find('.js-discount');

      const handleUpdate = defaultVal => e => {
        if (!$(input).val() && e.type === 'change') {
          $(input).val(defaultVal);
        }
        const seats = $(input).val() ? parseInt($(input).val()) : 1;
        monthText.text(`$${formatSmall(price * seats)}`);
        yearText.text(
          `$${formatSmall(annualPrice * seats * (1 - discount / 100))}`
        );
        // discountText.text(`$${formatSmall(annualPrice * seats)}`);
      };

      input.on('input change', handleUpdate('1'));
    });
  }
}
