export default function accordions($) {
  const pricingTables = $('.c-pt');
  const accs = $('.js-acc');

  if (pricingTables) {
    pricingTables.each((_, pt) => {
      const component = $(pt).find('.c-pt__expand-wrapper');
      const header = $('.header');
      const expandBtn = $(pt).find('.js-expand-table');
      const tableWrap = $(pt).find('.js-table-wrap');

      tableWrap[0].style.setProperty(
        '--table-height',
        `${tableWrap.find('.c-pt__table').outerHeight(true) / 10}rem`
      );

      $(document).ready(() => {
        const hash = window.location.hash.substr(1);
        if (hash && hash === 'compare') {
          expandBtn.removeClass('closed').addClass('open');

          tableWrap[0].style.setProperty(
            '--table-height',
            `${tableWrap.find('.c-pt__table').outerHeight(true) / 10}rem`
          );
          tableWrap.removeClass('closed').addClass('open');

          window.scrollTo(0, component.offset().top - header.outerHeight(true));
        }
      });

      expandBtn.on('click', function() {
        const hash = window.location.hash.substr(1);
        if (!hash && !$(this).hasClass('open')) {
          window.location.hash = 'compare';
        }

        tableWrap[0].style.setProperty(
          '--table-height',
          `${tableWrap.find('.c-pt__table').outerHeight(true) / 10}rem`
        );

        $(this)
          .toggleClass('closed')
          .toggleClass('open');

        tableWrap.toggleClass('closed').toggleClass('open');

        tableWrap[0].addEventListener('transitionend', () => {
          if (tableWrap.hasClass('open')) {
            tableWrap[0].style.setProperty('--table-height', 'auto');
          }
        });
      });

      const groups = $(pt).find('.c-pt__group');

      groups.each((_, g) => {
        const btn = $(g).find('.js-expander--trigger');
        const content = g.querySelector('.js-expander');
        content.style.setProperty(
          '--expand-to-height',
          `${content
            .querySelector('.js-expander__inner')
            .getBoundingClientRect().height / 10}rem`
        );
        $(btn).on('click', function() {
          $(this)
            .toggleClass('closed')
            .toggleClass('open');

          content.style.setProperty(
            '--expand-to-height',
            `${content
              .querySelector('.js-expander__inner')
              .getBoundingClientRect().height / 10}rem`
          );

          $(content)
            .toggleClass('closed')
            .toggleClass('open');
        });
      });
    });
  }

  if (accs) {
    accs.each((_, ac) => {
      const sections = $(ac).find('.js-item');
      sections.each((i, section) => {
        const btn = $(section).find('.js-expand-item');
        const content = section.querySelector('.js-content');

        btn.on('click', function() {
          $(this)
            .closest('.js-item')
            .toggleClass('closed')
            .toggleClass('open');

          const height = content
            .querySelector('.js-content-inner')
            .getBoundingClientRect().height;
          content.style.setProperty('--expand-to-height', `${height / 10}rem`);

          sections.each((j, section) => {
            if (j !== i) {
              $(section)
                .removeClass('open')
                .addClass('closed');
            }
          });
        });
      });
    });
  }
}
