const ajaxContent = $ => (
  getURL,
  callback = false,
  scrollUp = false,
  wrapperID = 'js-ajax-content'
) => {
  // Add filtering class
  $(`#${wrapperID}`).addClass('is-filtering');

  $.ajax(getURL, {
    dataType: 'html',
    success: function(data) {
      let currentData = $(`#${wrapperID}`);
      let newData = $(data).find(`#${wrapperID}`);

      // Udpate content with new html
      currentData.html(newData.html());

      $(`#${wrapperID} .u-watch`).each(function() {
        $(this).addClass('u-watch--active');
      });

      $(`#${wrapperID}`).removeClass('is-filtering');

      // If a callback function is provided, run it
      if (typeof callback === 'function') {
        callback(data);
      }

      // Scroll up
      if (scrollUp) {
        const container = $(`#${wrapperID}`);
        const header = $('.header');
        window.scrollTo(0, container.offset().top - header.outerHeight(true));
      }

      // Focus first link in updated content
      // const container = document.getElementById(wrapperID);
      // const firstEl = container.querySelector('a');
      // if (firstEl) {
      //   firstEl.focus();
      // }

      // Anounce the Page that was loaded, update the title
      // let $content = $(data).find(`#js-announce-message`);
      // let title;

      // if ($content) {
      //   title = $content.attr('data-announceCount');
      // } else {
      //   title = $(data)
      //     .filter('title')
      //     .text();
      //   document.title = title;
      // }

      // console.log(title);

      // announce(`Content updated - ${title}`, 'assertive');
    },
  });
};

export default ajaxContent;
