import 'babel-polyfill';

const LEVER_ENDPOINT_URL =
  'https://api.lever.co/v0/postings/quotapath?mode=json';
const ajaxId = 'ajax-jobs-content';
const DESCRIPTION_LENGTH = 140;

const createJobs = $ => _data => {
  //Checking for potential Lever source or origin parameters
  const pageUrl = window.location.href;
  let leverParameter = '';
  let trackingPrefix = '?lever-';

  if (pageUrl.indexOf(trackingPrefix) >= 0) {
    // Found Lever parameter
    let pageUrlSplit = pageUrl.split(trackingPrefix);
    leverParameter = '?lever-' + pageUrlSplit[1];
  }

  const jobs = _data.map((posting, index) => {
    const title = posting.text;
    const description = posting.descriptionPlain;
    //Making each job description shorter than 250 characters
    const shortDescription =
      $.trim(description)
        .substring(0, DESCRIPTION_LENGTH)
        .replace('\n', ' ') + '...';
    const location = posting.categories.location;
    const commitment = posting.categories.commitment;
    // const team = posting.categories.team;
    const link = posting.hostedUrl + leverParameter;

    return `
      <div class="c-jbp c-card-outer fade-in ani-${Math.floor(index / 3) + 1}">
        <div class="c-card u-p--rel">
          <div class="c-card--content">
            <a
              class="u-cwa"
              href="${link}"
              target="_BLANK"
            >
              <h2>${title}</h2>
            </a>

            <span class="f-text--caption">${location} • ${commitment}</span>

            <p>${shortDescription}</p>

            <div class="c-cta">
              <a class="c-btn c-btn--sky" href="">Learn More</a>
            </div>
          </div>
        </div>
      </div>
    `;
  });

  $(`#${ajaxId} .c-grid__inner`).append(jobs);
};

export default function initCareersAjax($) {
  const ajaxWrapper = document.getElementById(ajaxId);
  if (!ajaxWrapper) {
    return;
  }
  const createLeverJobs = createJobs($);

  $(`#${ajaxId}`).addClass('is-filtering');

  //Fetching job postings from Lever's postings API
  $.ajax({
    dataType: 'json',
    url: LEVER_ENDPOINT_URL,
    success: function(data) {
      createLeverJobs(data);
      setTimeout(() => {
        $(`#${ajaxId} .c-grid__inner`).addClass('u-watch u-watch--active');
        $(`#${ajaxId}`).removeClass('is-filtering');
      }, 50);
    },
  });
}
