  //       $('header.u-p--stick').css("top", `${mutation.addedNodes[0].getBoundingClientRect().height / 10}rem`)
  //       console.log(mutation.addedNodes[0].getAttribute('name') === "intercom-banner-frame");
  //     }
  //     // if(mutation.type === "childList" && mutation.removedNodes.length) {
  //     //   console.log(mutation)
  //     // }
  //     if(mutation.type === "attributes" && mutation.attributeName === "style") {
  //       mutation.target.style.maxHeight = null;
