import initMobileTabs from './mobileTabs';

const tabJsClass = 'js-tab';

function initTabs(tabContainer, watchUrl = false) {
  tabContainer.forEach(tabHeader => {
    initMobileTabs(tabHeader);
    const tabs = Array.from(tabHeader.getElementsByClassName(tabJsClass));

    if (watchUrl) {
      document.addEventListener('DOMContentLoaded', () => {
        const hash = window.location.hash.substr(1);
        if (hash) {
          tabs.forEach(tab => {
            if (tab.id.replace('-tab', '') === hash) {
              tab.classList.add('c-tab--active');
            } else {
              tab.classList.remove('c-tab--active');
              tab.classList.add('c-tab--inactive');
            }
          });
        }
      });
    }

    tabs.forEach(tab => {
      tab.addEventListener('click', function(e) {
        const tabNumber = e.target.getAttribute('data-tab');

        tabs.forEach((tab, index) => {
          if (index === tabNumber) return;
          tab.classList.remove('c-tab--active');
          tab.classList.add('c-tab--inactive');
        });
        e.target.classList.add('c-tab--active');
        e.target.classList.remove('c-tab--inactive');
      });
    });
  });
}

export default function tabs() {
  const tabHeaders = document.querySelectorAll('.c-ath');
  const blogListing = document.querySelectorAll('.c-bll');

  if (tabHeaders) {
    initTabs(tabHeaders, true);
  }

  if (blogListing) {
    blogListing.forEach(blogListing => {
      initMobileTabs(blogListing);
    });
  }
}
