export default function mobileTabs(parent, onClick) {
  const mTBtn = Array.from(parent.getElementsByClassName('js-dropbtn'))[0];
  const mTContain = Array.from(parent.getElementsByClassName('js-m-tabs'))[0];

  if (mTBtn && mTContain) {
    const tabs = Array.from(mTContain.getElementsByClassName('js-m-tab'));
    const btnLabel = Array.from(
      mTBtn.getElementsByClassName('js-dropbtn-title')
    )[0];

    mTBtn.addEventListener('click', () => {
      // e.stopPropagation();
      // e.preventDefault();

      mTContain.classList.toggle('open');
    });

    mTBtn.addEventListener('blur', () => {
      // e.stopPropagation();
      // e.preventDefault();
      mTContain.classList.remove('open');
    });

    const handleClick = e => {
      btnLabel.innerText = e.target.innerText;
      mTContain.classList.remove('open');
      if (onClick) {
        const tabNumber = e.target.getAttribute('data-tab');
        onClick(tabNumber);
      }
    };
    tabs.forEach(tab => {
      tab.addEventListener('mousedown', handleClick, false);
      tab.addEventListener('touchdown', handleClick, false);
    });
  }
}
