import initMobileTabs from './mobileTabs';

const showcaseClass = '.js-showcase';
const tabJsClass = 'js-tab';
const slideClass = 'c-showcase__slide';

export default function showcase() {
  const showcases = document.querySelectorAll(showcaseClass);
  if (showcases) {
    showcases.forEach(showcase => {
      const slides = Array.from(showcase.getElementsByClassName(slideClass));
      const tabs = Array.from(showcase.getElementsByClassName(tabJsClass));

      const handleUpdateSlide = tabNumber => {
        slides.forEach((slide, index) => {
          if (index === tabNumber) return;
          slide.classList.remove('c-slide--active');
          slide.classList.add('c-slide--inactive');
        });

        slides[tabNumber].classList.add('c-slide--active');
        slides[tabNumber].classList.remove('c-slide--inactive');
      };

      tabs.forEach(tab => {
        tab.addEventListener('click', function(e) {
          e.stopPropagation();
          e.preventDefault();
          const tabNumber = e.target.getAttribute('data-tab');

          tabs.forEach((tab, index) => {
            if (index === tabNumber) return;
            tab.classList.remove('c-tab--active');
            tab.classList.add('c-tab--inactive');
          });
          e.target.classList.add('c-tab--active');
          e.target.classList.remove('c-tab--inactive');
          handleUpdateSlide(tabNumber);
        });
      });
      initMobileTabs(showcase, handleUpdateSlide);
    });
  }
}
