import ajaxContent from './ajax-content';
import initTabs from './tabs';

export default function initPaginationAjax($) {
  const ajaxWrapper = document.getElementById('js-ajax-content');

  if (!ajaxWrapper) {
    return;
  }

  const pageBtns = document.querySelectorAll('.js-pagination-link');

  if (!pageBtns) {
    return;
  }

  function handleAjaxClick(e) {
    e.preventDefault();
    let href = $(this).attr('href');
    let parser = document.createElement('a');
    parser.href = href;
    let updateURL = parser.pathname + parser.search;
    history.pushState(null, null, updateURL);

    ajaxContent($)(href, initTabs, true);
  }

  $(document).on('click', '.js-pagination-link', handleAjaxClick);
  $(document).on('touchdown', '.js-pagination-link', handleAjaxClick);
  $(document).on('mousedown', '.js-pagination-link', handleAjaxClick);
}
